.spinner__overlay {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  background-color: rgba(0, 0, 0, .25);
  transition: opacity .15s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner__overlay.is-hidden {
  opacity: 0;
  visibility: hidden;
}

.spinner--on {
  width: 100px;
  height: 100px;
  z-index: 999;
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.spinner--on div {
  box-sizing: border-box;
  width: 75px;
  height: 75px;
  border: 8px solid rgba(0, 0, 0, 0);
  border-top-color: #ff6b08;
  border-radius: 50%;
  margin: 8px;
  animation: spinner 1.2s cubic-bezier(.5, 0, .5, 1) infinite;
  display: block;
  position: absolute;
}

.spinner--on div:nth-child(1) {
  animation-delay: -450ms;
}

.spinner--on div:nth-child(2) {
  animation-delay: -300ms;
}

.spinner--on div:nth-child(3) {
  animation-delay: -150ms;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

